import React from 'react';
import { QuestionGroup } from '..';

function Column({ side, data }) {
	return (
		<div>
			{data &&
				data.groups[side].map((groupsElem, id) => {
					const questions = data.questions.filter(elem => elem.groupId === groupsElem.id);
					if (questions.length > 0) {
						return <QuestionGroup key={id} header={groupsElem.name} questions={questions} />;
					}
				})}
		</div>
	);
}

export default Column;
