import React, { useState, useRef, useEffect } from 'react';

import { HashRouter as Link, useLocation } from 'react-router-dom';

import './css/styles.css';

function QuestionContent({ title, content, id }) {
	const contentBox = useRef();
	const [isUnFolded, setIsUnFolded] = useState(false);

	const showContent = () => {
		const content = contentBox.current;
		content.style.height = content.scrollHeight + 'px';
	};

	const hideContent = () => {
		const content = contentBox.current;
		content.style.height = null;
	};

	const handleFold = () => {
		setIsUnFolded(!isUnFolded);
		!isUnFolded ? showContent() : hideContent();
	};

	let location = useLocation();

	useEffect(() => {
		const pathname = location.pathname;
		const separator = pathname.indexOf('_');
		const questionId = pathname.slice(1, separator);
		console.log(questionId);
		if (questionId == id) {
			showContent();
			setIsUnFolded(true);
		}
	}, []);

	return (
		<div className="question">
			<a href={`#${id}_${title.friendlyfyUrl()}`}>
				<div className="header" onClick={handleFold}>
					<h3>{title}</h3>
					<span className="material-icons-outlined">{isUnFolded ? 'expand_less' : 'expand_more'}</span>
				</div>
			</a>
			<div className="content" ref={contentBox} dangerouslySetInnerHTML={{ __html: content }}></div>
		</div>
	);
}

export default QuestionContent;
