import React from 'react';

import { Question } from '..';

import './css/styles.css';

function QuestionGroup({ header, questions }) {
	return (
		<div className="box question-group">
			{<h2>{header}</h2>}
			{questions.map((elem, id) => (
				<Question title={elem.title} key={id} content={elem.content} id={elem.id} />
			))}
		</div>
	);
}

export default QuestionGroup;
