import React, { useRef } from 'react';

import './css/styles.css';

function SearchInput({ onSearch, onReset }) {
	const inputRef = useRef();

	return (
		<div className="search-input">
			<form onSubmit={event => onSearch(event, inputRef)}>
				<input ref={inputRef} type="text" placeholder="search" />
				<div className="buttons">
					<button className="buttons__btn buttons__btn--search" type="submit">
						SEARCH
					</button>
					<button className="buttons__btn buttons__btn--reset" type="reset" onClick={onReset}>
						RESET
					</button>
				</div>
			</form>
		</div>
	);
}

export default SearchInput;
