import React, { StrictMode } from 'react';
import ReactDOM from 'react-dom';

import App from './App';

const rootElement = document.getElementById('root');

String.prototype.friendlyfyUrl = function () {
	return this.replaceAll(' ', '-').replace(/[!"#$%&'()*+,./:;<=>?@[\]^`{|}~]/g, '');
};

ReactDOM.render(
	<StrictMode>
		<App />
	</StrictMode>,
	rootElement
);
