import React, { useEffect, useState } from 'react';

import './css/index.css';

import { Column, SearchInput } from './Components';

export default function App() {
	const [rawData, setRawData] = useState();
	const [filteredData, setFilteredData] = useState();

	useEffect(() => {
		getFaqData();
	}, []);

	const sortQuestions = data => {
		data.groups.left = data.groups.left.sort((a, b) => {
			return a.id - b.id;
		});
		data.groups.right = data.groups.right.sort((a, b) => {
			return a.id - b.id;
		});
		return data;
	};

	const getFaqData = () => {
		fetch('02-faq.json')
			.then(response => response.json())
			.then(data => {
				setRawData(data);
				setFilteredData(sortQuestions(data));
			});
	};

	const handleSearch = (event, inputRef) => {
		event.preventDefault();

		const searchInput = inputRef.current.value.toLowerCase();
		const searchWords = searchInput.split(' ');
		const filteredQuestions = rawData.questions.filter(elem => {
			for (let i = 0; i < searchWords.length; i++) {
				console.log(elem);
				return elem.title.toLowerCase().includes(searchWords[i]) || elem.content.toLowerCase().includes(searchWords[i]);
			}
		});
		setFilteredData({ groups: rawData.groups, questions: filteredQuestions });
	};

	const handleReset = () => {
		setFilteredData(sortQuestions(rawData));
	};

	return (
		<div className="App">
			<header className="box">
				<h1>
					<a href="/Simplemining-recruitment-task-build/">FAQ</a>
				</h1>
				<SearchInput onSearch={handleSearch} onReset={handleReset} />
			</header>
			<div className="columns">
				<div className="column">
					<Column side="left" data={filteredData} />
				</div>
				<div className="column">
					<Column side="right" data={filteredData} />
				</div>
			</div>
		</div>
	);
}
